@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --bgGray: #ffffff10;
    --bgDark: #212121;
    --bgGray-20: #ffffff05;
    --coloGray: #ffffff7a;

    --white: #ffffff;
    --time-delay-300: .3s;
}

.main {
    padding: 2.5em 0em;
    color: var(--white);
}

.bgDark {
    background-color: var(--bgDark);
}

.main h1 {
    font-size: 32px;
    padding-bottom: .5em;
}

.main article section {
    width: 100%;
}

.main article section.sectionCI {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.main p {
    text-align: justify;
    line-height: 1.5em;
    padding-bottom: .5em;
}

.main section p:nth-last-child(1) {
    padding-bottom: 0;
}

.habilidades>a {
    display: flex;
}

h2 {
    text-align: center;
}

.bgGray {
    background-color: var(--bgGray-20);
}

.bgGray {
    background-color: var(--bgGray);
}

.colorGray {
    color: var(--coloGray);
}

.pt3 {
    padding-top: 2em;
}

.about>p {
    color: #ffffffcc;
}

.about>h2 {
    padding-bottom: .5em;
}

.about a {
    font-weight: bolder;
    text-decoration: none;
}

.about a:hover {
    transition: all .35s ease-in !important;
}

.link-about {
    @apply no-underline
}

.link-about:hover {
    @apply text-violet-600
}

.skill-item {
    -webkit-transition: var(--time-delay-300) ease-out;
    -moz-transition: var(--time-delay-300) ease-out;
    -o-transition: var(--time-delay-300) ease-out;
    transition: var(--time-delay-300) ease-out;
    background-color: var(--bgGray);
    @apply flex flex-col flex-wrap justify-center rounded-md shadow-md py-2 pt-4 text-center text-neutral-400 bg-zinc-800;
}

.skill-title {
    @apply pt-3 text-sm
}

.skill-icon {
    @apply flex justify-center text-5xl
}

.skill-item:hover {
    @apply bg-zinc-700 text-gray-100 shadow-lg shadow-stone-900
}


.skill-item:hover .skill-icon,
.skill-item:hover .skill-title {
    -webkit-transition: var(--time-delay-300) ease-out;
    -moz-transition: var(--time-delay-300) ease-out;
    -o-transition: var(--time-delay-300) ease-out;
    transition: var(--time-delay-300) ease-out;
    @apply font-bold;
}

.skill-item:hover .skill-icon.html,
.skill-item:hover .skill-title.html {
    @apply text-orange-600;
}

.skill-item:hover .skill-icon.css,
.skill-item:hover .skill-title.css {
    @apply text-blue-500;
}

.skill-item:hover .skill-icon.js,
.skill-item:hover .skill-title.js {
    @apply text-yellow-500;
}

.skill-item:hover .skill-icon.ts,
.skill-item:hover .skill-title.ts {
    @apply text-blue-600;
}

.skill-item:hover .skill-icon.node,
.skill-item:hover .skill-title.node {
    @apply text-green-600;
}

.skill-item:hover .skill-icon.java,
.skill-item:hover .skill-title.java {
    @apply text-red-500;
}

.skill-item:hover .skill-icon.spring,
.skill-item:hover .skill-title.spring {
    @apply text-green-500;
}

.skill-item:hover .skill-icon.mysql,
.skill-item:hover .skill-title.mysql {
    @apply text-blue-500;
}

.skill-item:hover .skill-icon.postgresql,
.skill-item:hover .skill-title.postgresql {
    @apply text-blue-500;
}

.skill-item:hover .skill-icon.react,
.skill-item:hover .skill-title.react {
    @apply text-blue-400;
}

.skill-item:hover .skill-icon.react-native,
.skill-item:hover .skill-title.react-native {
    @apply text-green-400;
}

.skill-item:hover .skill-icon.docker,
.skill-item:hover .skill-title.docker {
    @apply text-sky-500;
}

.skill-item:hover .skill-icon.git,
.skill-item:hover .skill-title.git {
    @apply text-orange-600;
}

.skill-item:hover .skill-icon.github,
.skill-item:hover .skill-title.github {
    @apply text-white;
}

.skill-item:hover .skill-icon.bootstrap,
.skill-item:hover .skill-title.bootstrap {
    @apply text-violet-500;
}

.skill-item:hover .skill-icon.jquery,
.skill-item:hover .skill-title.jquery {
    @apply text-blue-500;
}

.skill-item:hover .skill-icon.yarn,
.skill-item:hover .skill-title.yarn {
    @apply text-blue-500;
}

.skill-item:hover .skill-icon.npm,
.skill-item:hover .skill-title.npm {
    @apply text-red-500;
}



.skill-item:hover .skill-title {
    @apply text-white
}

.skill-item:hover .html {
    @apply text-red-500;
}



.link-project {
    -webkit-transition: var(--time-delay-300) ease-out;
    -moz-transition: var(--time-delay-300) ease-out;
    -o-transition: var(--time-delay-300) ease-out;
    transition: var(--time-delay-300) ease-out;
    @apply inline-block box-content w-24 p-2 my-2 text-center decoration-transparent rounded-lg shadow-sm bg-violet-900 text-slate-300 font-extrabold
}

.link-project:hover {
    -webkit-transition: var(--time-delay-300) ease-in;
    -moz-transition: var(--time-delay-300) ease-in;
    -o-transition: var(--time-delay-300) ease-in;
    transition: var(--time-delay-300) ease-in;
    @apply bg-violet-800 text-white font-extrabold
}

.link-project .title-project {
    @apply text-lg font-bold
}

.link-project .text-project {
    @apply text-gray-400 text-xs font-bold text-justify pt-2
}

.link-project:hover .text-project {
    -webkit-transition: var(--time-delay-300) ease-in;
    -moz-transition: var(--time-delay-300) ease-in;
    -o-transition: var(--time-delay-300) ease-in;
    transition: var(--time-delay-300) ease-in;
    @apply text-gray-100 font-bold
}

.card-project {
    -webkit-transition: var(--time-delay-300) ease-out;
    -moz-transition: var(--time-delay-300) ease-out;
    -o-transition: var(--time-delay-300) ease-out;
    transition: var(--time-delay-300) ease-out;
    @apply max-w-sm rounded-lg overflow-hidden shadow-md bg-zinc-900 shadow-black sm:shadow-transparent
}

.card-project:hover {
    -webkit-transition: var(--time-delay-300) ease-in;
    -moz-transition: var(--time-delay-300) ease-in;
    -o-transition: var(--time-delay-300) ease-in;
    transition: var(--time-delay-300) ease-in;

    @apply md:shadow-black
}

.tag-project {
    @apply inline-block bg-zinc-800 shadow-lg rounded-full px-3 py-1 text-sm font-semibold text-gray-400 mr-2 mb-2
}